import React, { useEffect, useState } from 'react';
import { Container, Typography, Grid, Card, CardContent, Button, Box } from '@mui/material';
import { useNavigate, useParams } from 'react-router-dom';
import Fuse from 'fuse.js';  //  Fuse.js
import { getMedicineCount, getMedicine } from '../contract';

const MedicineList = () => {
  const { name } = useParams();  //  (commonName)  URL
  const navigate = useNavigate();  // 
  const [medicines, setMedicines] = useState([]);
  const [filteredMedicines, setFilteredMedicines] = useState([]); //  state 
  const [medicineCount, setMedicineCount] = useState(0);

  //  Smart Contract
  const fetchMedicineCount = async () => {
    const count = await getMedicineCount();
    setMedicineCount(count);
  };

  // 
  const fetchMedicines = async () => {
    let medicineArray = [];
    for (let i = 1; i <= medicineCount; i++) {
      const medicine = await getMedicine(i);  // 
      medicineArray.push(medicine);
    }
    setMedicines(medicineArray);
  };

  //  Fuse.js  fuzzy search
  const searchMedicine = () => {
    if (name && medicines.length > 0) {
      const fuse = new Fuse(medicines, {
        keys: ['commonName', 'tradeName'], //  commonName  tradeName
        threshold: 0.4, //  threshold  ()
      });

      const result = fuse.search(name);
      if (result.length > 0) {
        setFilteredMedicines(result.map(res => res.item));  // 
      } else {
        setFilteredMedicines([]);  // 
      }
    } else {
      setFilteredMedicines(medicines); //  
    }
  };

  useEffect(() => {
    fetchMedicineCount();
  }, []);

  useEffect(() => {
    if (medicineCount > 0) {
      fetchMedicines();
    }
    // eslint-disable-next-line
  }, [medicineCount]);

  useEffect(() => {
    if (medicines.length > 0) {
      console.log(":", name); // 
      console.log(":", medicines); // 
      searchMedicine(); // 
    }
    // eslint-disable-next-line
  }, [name, medicines]);

  return (
    <Container maxWidth="lg" sx={{ paddingTop: 3 }}>
      <Typography
        variant="h4"
        gutterBottom
        sx={{
          textAlign: 'center',
          marginBottom: 3,
          color: '#89A8B2',  // 
          fontWeight: 'bold',
        }}
      >
        {name ? `: ${name}` : ''}
      </Typography>
      <Grid container spacing={3} justifyContent="center">
        {filteredMedicines.length > 0 ? (
          filteredMedicines.map((medicine, index) => (
            <Grid item xs={12} sm={6} md={8} lg={6} key={index}>
              <Card sx={{ padding: 3, boxShadow: 3, borderRadius: 3, width: '100%', maxWidth: '800px', backgroundColor: '#E5E1DA' }}>
                <CardContent>
                  {/*  */}
                  <Grid container spacing={2}>
                    <Grid item xs={4}>
                      <Typography variant="body1" fontWeight="bold">:</Typography>
                    </Grid>
                    <Grid item xs={8}>
                      <Typography variant="body1" sx={{ whiteSpace: 'normal', wordWrap: 'break-word' }}>
                        {medicine.commonName}
                      </Typography>
                    </Grid>

                    <Grid item xs={4}>
                      <Typography variant="body1" fontWeight="bold">:</Typography>
                    </Grid>
                    <Grid item xs={8}>
                      <Typography variant="body1" sx={{ whiteSpace: 'normal', wordWrap: 'break-word' }}>
                        {medicine.tradeName}
                      </Typography>
                    </Grid>

                    <Grid item xs={4}>
                      <Typography variant="body1" fontWeight="bold">:</Typography>
                    </Grid>
                    <Grid item xs={8}>
                      <Typography variant="body1" sx={{ whiteSpace: 'normal', wordWrap: 'break-word' }}>
                        {medicine.dosageForm}
                      </Typography>
                    </Grid>

                    <Grid item xs={4}>
                      <Typography variant="body1" fontWeight="bold">:</Typography>
                    </Grid>
                    <Grid item xs={8}>
                      <Typography variant="body1" sx={{ whiteSpace: 'normal', wordWrap: 'break-word' }}>
                        {medicine.usage}
                      </Typography>
                    </Grid>

                    <Grid item xs={4}>
                      <Typography variant="body1" fontWeight="bold">:</Typography>
                    </Grid>
                    <Grid item xs={8}>
                      <Typography variant="body1" sx={{ whiteSpace: 'normal', wordWrap: 'break-word' }}>
                        {medicine.doctorInfo}
                      </Typography>
                    </Grid>

                    <Grid item xs={4}>
                      <Typography variant="body1" fontWeight="bold">:</Typography>
                    </Grid>
                    <Grid item xs={8}>
                      <Typography variant="body1" sx={{ whiteSpace: 'normal', wordWrap: 'break-word' }}>
                        {medicine.commonSideEffects}
                      </Typography>
                    </Grid>

                    <Grid item xs={4}>
                      <Typography variant="body1" fontWeight="bold">:</Typography>
                    </Grid>
                    <Grid item xs={8}>
                      <Typography variant="body1" sx={{ whiteSpace: 'normal', wordWrap: 'break-word' }}>
                        {medicine.immediateSideEffects}
                      </Typography>
                    </Grid>

                    <Grid item xs={4}>
                      <Typography variant="body1" fontWeight="bold">:</Typography>
                    </Grid>
                    <Grid item xs={8}>
                      <Typography variant="body1" sx={{ whiteSpace: 'normal', wordWrap: 'break-word' }}>
                        {medicine.storageMethod}
                      </Typography>
                    </Grid>
                  </Grid>
                </CardContent>
              </Card>
            </Grid>
          ))
        ) : (
          <Typography variant="h6" sx={{ textAlign: 'center', marginTop: 3, color: '#B3C8CF' }}>
            
          </Typography>
        )}
      </Grid>

      {/*  */}
      <Box sx={{ textAlign: 'center', marginTop: 4 }}>
        <Button
          variant="outlined"
          color="primary"
          onClick={() => navigate('/')}  // 
          sx={{
            padding: '10px 20px',
            borderRadius: 3,
            borderColor: '#89A8B2',
            color: '#89A8B2',
            '&:hover': {
              backgroundColor: '#B3C8CF',
              borderColor: '#89A8B2',
            },
          }}
        >
          
        </Button>
      </Box>
    </Container>
  );
};

export default MedicineList;
