import React, { useState } from 'react';
import { Button, TextField, Container, Typography, Box } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import { addMedicine } from '../contract';

const Input = () => {
  const [medicineData, setMedicineData] = useState({
    commonName: '',        // 
    tradeName: '',         // 
    dosageForm: '',        // 
    usage: '',             // 
    doctorInfo: '',        // 
    missedDose: '',        // 
    commonSideEffects: '', // 
    immediateSideEffects: '', // 
    storage: ''            // 
  });

  const navigate = useNavigate();

  const handleChange = (e) => {
    const { name, value } = e.target;
    setMedicineData({ ...medicineData, [name]: value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    // 
    for (const key in medicineData) {
      if (medicineData[key] === '') {
        alert(` ${key}`);
        return;
      }
    }

    try {
      //  smart contract
      await addMedicine(medicineData);

      // 
      alert('!');

      //  Home 
      navigate('/'); //  navigate('/:id')  ID 

    } catch (error) {
      // 
      alert(' ');
    }
  };

  return (
    <Container
      maxWidth="lg"
      sx={{
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'center',
        alignItems: 'center',
        minHeight: '100vh',
        backgroundColor: '#E3F2FD', // 
        padding: 3,
        borderRadius: 4,
        boxShadow: 3,
      }}
    >
      <Typography
        variant="h4"
        gutterBottom
        sx={{ 
          fontWeight: 'bold',
          textAlign: 'center',
          marginBottom: 3,
          color: '#0D47A1', // 
        }}
      >
        
      </Typography>
      <form onSubmit={handleSubmit} style={{ width: '100%' }}>
        <Box
          sx={{
            display: 'flex',
            flexDirection: 'column',
            gap: 2,
          }}
        >
          <TextField
            label=""
            variant="outlined"
            fullWidth
            name="commonName"
            value={medicineData.commonName}
            onChange={handleChange}
            margin="normal"
            multiline
            sx={{
              borderRadius: 2,
              backgroundColor: '#E3F2FD', // 
              '& .MuiOutlinedInput-root': {
                '& fieldset': {
                  borderColor: '#64B5F6', // 
                },
                '&:hover fieldset': {
                  borderColor: '#42A5F5', //  hover
                },
              },
            }}
          />
          <TextField
            label=""
            variant="outlined"
            fullWidth
            name="tradeName"
            value={medicineData.tradeName}
            onChange={handleChange}
            margin="normal"
            multiline
            sx={{
              borderRadius: 2,
              backgroundColor: '#E3F2FD', // 
              '& .MuiOutlinedInput-root': {
                '& fieldset': {
                  borderColor: '#64B5F6', // 
                },
                '&:hover fieldset': {
                  borderColor: '#42A5F5', //  hover
                },
              },
            }}
          />
          <TextField
            label=""
            variant="outlined"
            fullWidth
            name="dosageForm"
            value={medicineData.dosageForm}
            onChange={handleChange}
            margin="normal"
            sx={{
              borderRadius: 2,
              backgroundColor: '#E3F2FD', // 
              '& .MuiOutlinedInput-root': {
                '& fieldset': {
                  borderColor: '#64B5F6', // 
                },
                '&:hover fieldset': {
                  borderColor: '#42A5F5', //  hover
                },
              },
            }}
          />
          <TextField
            label=""
            variant="outlined"
            fullWidth
            name="usage"
            value={medicineData.usage}
            onChange={handleChange}
            margin="normal"
            multiline
            sx={{
              borderRadius: 2,
              backgroundColor: '#E3F2FD', // 
              '& .MuiOutlinedInput-root': {
                '& fieldset': {
                  borderColor: '#64B5F6', // 
                },
                '&:hover fieldset': {
                  borderColor: '#42A5F5', //  hover
                },
              },
            }}
          />
          <TextField
            label=""
            variant="outlined"
            fullWidth
            name="doctorInfo"
            value={medicineData.doctorInfo}
            onChange={handleChange}
            margin="normal"
            multiline
            sx={{
              borderRadius: 2,
              backgroundColor: '#E3F2FD', // 
              '& .MuiOutlinedInput-root': {
                '& fieldset': {
                  borderColor: '#64B5F6', // 
                },
                '&:hover fieldset': {
                  borderColor: '#42A5F5', //  hover
                },
              },
            }}
          />
          <TextField
            label=""
            variant="outlined"
            fullWidth
            name="missedDose"
            value={medicineData.missedDose}
            onChange={handleChange}
            margin="normal"
            multiline
            sx={{
              borderRadius: 2,
              backgroundColor: '#E3F2FD', // 
              '& .MuiOutlinedInput-root': {
                '& fieldset': {
                  borderColor: '#64B5F6', // 
                },
                '&:hover fieldset': {
                  borderColor: '#42A5F5', //  hover
                },
              },
            }}
          />
          <TextField
            label=""
            variant="outlined"
            fullWidth
            name="commonSideEffects"
            value={medicineData.commonSideEffects}
            onChange={handleChange}
            margin="normal"
            multiline
            sx={{
              borderRadius: 2,
              backgroundColor: '#E3F2FD', // 
              '& .MuiOutlinedInput-root': {
                '& fieldset': {
                  borderColor: '#64B5F6', // 
                },
                '&:hover fieldset': {
                  borderColor: '#42A5F5', //  hover
                },
              },
            }}
          />
          <TextField
            label=""
            variant="outlined"
            fullWidth
            name="immediateSideEffects"
            value={medicineData.immediateSideEffects}
            onChange={handleChange}
            margin="normal"
            multiline
            sx={{
              borderRadius: 2,
              backgroundColor: '#E3F2FD', // 
              '& .MuiOutlinedInput-root': {
                '& fieldset': {
                  borderColor: '#64B5F6', // 
                },
                '&:hover fieldset': {
                  borderColor: '#42A5F5', //  hover
                },
              },
            }}
          />
          <TextField
            label=""
            variant="outlined"
            fullWidth
            name="storage"
            value={medicineData.storage}
            onChange={handleChange}
            margin="normal"
            multiline
            sx={{
              borderRadius: 2,
              backgroundColor: '#E3F2FD', // 
              '& .MuiOutlinedInput-root': {
                '& fieldset': {
                  borderColor: '#64B5F6', // 
                },
                '&:hover fieldset': {
                  borderColor: '#42A5F5', //  hover
                },
              },
            }}
          />
        </Box>
        
        <Button
          type="submit"
          variant="contained"
          color="primary"
          fullWidth
          sx={{
            marginTop: 3,
            padding: '10px',
            borderRadius: 4,
            backgroundColor: '#1E88E5', // 
            color: '#FFFFFF', // 
            '&:hover': {
              backgroundColor: '#1565C0', //  hover
            },
          }}
        >
          
        </Button>

        {/*  Home */}
        <Button
          variant="outlined"
          color="secondary"
          fullWidth
          sx={{
            marginTop: 2,
            padding: '10px',
            borderRadius: 4,
            borderColor: '#1E88E5',
            color: '#1E88E5', // 
            '&:hover': {
              backgroundColor: '#E3F2FD', //  hover
            },
          }}
          onClick={() => navigate('/')} //  Home
        >
          
        </Button>
      </form>
    </Container>
  );
};

export default Input;
